import React from 'react';
import Lottie from 'react-lottie';
import LottieOne from "../media/json/lottie1.json";

const Back_Auth= () => {
    const defaultOptions = {
        loop: true,
        autoplay: true, 
        animationData: LottieOne,
        rendererSettings: {
          preserveAspectRatio: 'xMidYMid slice'
        }
      };
    const style = {
        position:'fixed',bottom:'-60px',
        height: 620,zIndex: '-1'
      };
    return (
        <>
        <Lottie id="lottieone" options={defaultOptions} style={style}/>
        </>
    );
};

export default Back_Auth;