import React, { useEffect,useState } from 'react';
import Navbar from '../components/Navbar';
import { connect } from 'react-redux';
import { checkAuthenticated, load_user,refresh_token } from '../actions/auth';
import client from '../dashboard/Sockettest';
import { useSelector } from 'react-redux';
const Layout = ({ checkAuthenticated, load_user, refresh_token, children }) => {
    const isAuth=useSelector(state=>state.auth.isAuthenticated)

    let [loading, setLoading] = useState(true)
    
    let updateToken = ()=> {
        refresh_token();
        if(loading){
            setLoading(false)
        }
    }
    useEffect(()=> {
        if(loading){
            updateToken()
        }
        let fourMinutes = 1000*60*4
        let interval =  setInterval(()=> {
            if(localStorage.getItem('refresh')){
                updateToken()
            }
        }, fourMinutes)
        return ()=> clearInterval(interval)
    }, [loading])
    
    useEffect(() => {
        checkAuthenticated();
        load_user();
        
    }, []);
    
    return (
        <div>
            
            <Navbar />
          
            {children}
        </div>
    );
};

export default connect(null, { checkAuthenticated, load_user, refresh_token })(Layout);