import React,{useState,useEffect} from 'react';

import { connect } from 'react-redux';
import TempProfile from '../dashboard/TempProfile';
import Info from "../dashboard/Info"
import Sockettest from '../dashboard/Sockettest';

import Grid from '@mui/material/Grid';
import { Typography, Button, Box, Stack } from '@mui/material';
import AppBar from '@mui/material/AppBar';
import Modal from '@mui/material/Modal';
import Dropdown from './dropdown';
import Photo from './bg.jpeg';
import { fontStyle } from '@mui/system';
const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};

const DashBaord= () => {
    const [open, setOpen] = React.useState(false);
    //<Info/>{localStorage.getItem("refresh")}
    
   
    return (
        <>
            <Grid container>
                <Grid item sm={4} style={{ backgroundColor: "#3D26EB", width:"100vh", height: "100vh", padding: "40px" }}>
                    <Typography style={{ fontSize: "32.7px", fontWeight: "bold" }}>Cancel Your Meals</Typography>
                    <Dropdown />
                    <Stack direction="column" spacing={4} style={{ padding: "40px 45px", width: "70%" }}>
                        <Button variant="contained" href="#contained-buttons" style={{ backgroundColor: "#17D059" }}>
                            Show Menu
                        </Button>
                        <Button variant="contained" href="#contained-buttons" style={{ backgroundColor: "#17D059" }}>
                            Cancel My Meal
                        </Button>
                    </Stack>
                </Grid>
                <Grid item sm={8} style={{ backgroundColor: "#17D059", height: "80vh" }}>
                    <img src={Photo} style={{ height: "73%", width: "88%", margin: "62px" }}></img>
                </Grid>
            </Grid>
     
        </>
    );
};




export default (DashBaord);