import React, { useState, useEffect } from 'react';

import { connect } from 'react-redux';
import { useSelector } from 'react-redux';

import {w3cwebsocket as W3CWebSocket} from "websocket";


const client = new W3CWebSocket("ws://localhost:8000/ws/socket-user/?token="+localStorage.getItem('access'));


const Sockettest= ({ isAuthenticated },props) => {
    
    const [isConnected, setIsConnected] = useState(false);
    const [onlineUsers,setOnlineUsers] = useState({});
    const [prevonlineUsers,setPrevOnlineUsers] = useState({});
    const [count,setCount] = useState(0);
    
    const isAuth=useSelector(state=>state.auth.access)
    useEffect(() => {
        client.onopen=()=>{
            if(isConnected===false){
              client.send(JSON.stringify({action: "list",request_id: new Date().getTime()}))
              setIsConnected(true)
              console.log("Firing") 
            }
        }
        client.onclose=()=>{
          setIsConnected(false)
          console.log("NotFiring")  
      }
       
        return ()=>{
          if(!localStorage.getItem('access')){
            console.log(localStorage.getItem('access'))
            close();
          }
 
        }
      },[]);

      useEffect(()=>{
        client.onmessage=e=>{
         
            if(JSON.parse(e.data).action==="list"){
              setPrevOnlineUsers(JSON.parse(e.data).data)
              setOnlineUsers(JSON.parse(e.data).data)
            }
            else if(JSON.parse(e.data).action==="update"){
              setOnlineUsers(JSON.parse(e.data).data)
              
              //update(JSON.parse(e.data).data.id,JSON.parse(e.data).data.is_online)
            } 
          }
          return ()=>{
            
          }
      
      },[]);

    const click=()=>{
      client.send(JSON.stringify({action: "list",request_id: new Date().getTime()}))
    }

    const update=(userid,online)=>{
      for (var i = 0; i < onlineUsers.length; i++) {
        if (onlineUsers[i].id === userid) {
          const current_state=onlineUsers;
          current_state[i].is_online = online;
          setOnlineUsers([...onlineUsers]);
          return;
        }
      }
    }

    const close=()=>{
      console.log("Closed")
      client.close()
  }
    
  return (
    <>
    <p style={{position: 'absolute', top: '10%', left: '30%'}}>{count}{JSON.stringify(onlineUsers)}</p>
    <button style={{position: 'absolute', top: '40%', left: '50%'}} onClick={click}>Send</button>
    
    </>
    
  )
}
const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});


export default connect(mapStateToProps)(Sockettest);