import React, {useState} from 'react';
import { Link, Navigate} from 'react-router-dom';
import { connect } from 'react-redux';
import { login} from '../actions/auth';
import DashBaord from './Dashboard';
import axios from 'axios';
import Back_Auth from '../decoration/Back_Auth'


const Login = ({login, isAuthenticated}) => {
    const [formData, setFormData] = useState({
        email: '',
        password: '' 
    });

    const { email, password } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        login(email, password);
    };

    if (isAuthenticated) {
        return <Navigate to='/' />
    }

    return (
        <>
       
        <div style={{position:'absolute', top:'0%',right:'0%',width:'60%', height:'100%',background:'#3D26EB'}}></div>
       <img src={require('./coffee.png')} style={{position: 'absolute', width: '60%',right:'0%'}} />
        <div id="signupdiv" style={{zIndex:'2'}}>
        <p style={{margin: '27.5px', fontFamily: '"Roboto", sans-serix', fontSize: '35px', color: 'rgba(52, 40, 57, 1)', letterSpacing: '1.5px', fontWeight: 600, marginBottom: '10px'}}>
          Log In
        </p>
        <form onSubmit={e => onSubmit(e)}>
        <div className="signupinput bigsignupinput">
          <input type="email" name="email" id="signupinputbox" placeholder="EMAIL"  autocomplete="off" value={email} onChange={e => onChange(e)}/>
        </div>
        <div className="signupinput bigsignupinput">
          <input type="password" name="password" id="signupinputbox" placeholder="PASSWORD" value={password} onChange={e => onChange(e)} />
        </div>
        <div className="signupinput bigsignupinput checkdiv">  
          <span htmlFor="check1" style={{fontWeight: 300, position: 'relative', top: '-2px', fontFamily: '"Roboto", sans-serif'}}> Forgot your Password? <Link to="/reset-password">Reset Password</Link></span>
        </div>
        <div id="signupinput" style={{width: '100%', marginLeft: '0px'}}>  
          <center><button className="button" style={{background:'#3D26EB'}} type='submit'>Login</button></center>
        </div>	
        </form>
      </div>
        </>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { login })(Login);