import React, { useState } from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { dividerClasses } from '@mui/material';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';

export default function BasicSelect() {
    const [days, setDays] = React.useState('');
    const [meals, setMeals] = React.useState('');

    const handleChangeDays = (event) => {
        setDays(event.target.value);
    };
    const handleChangeMeals = (event) => {
        setMeals(event.target.value);
    };

    return (
        <div>
            <Box sx={{ minWidth: 120 }} style={{ marginTop: "30px" ,}}>
                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">Days</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={days}
                        label="Days"
                        onChange={handleChangeDays}
                    >
                        <MenuItem value="Monday">Monday</MenuItem>
                        <MenuItem value="Tuesday">Tuesday</MenuItem>
                        <MenuItem value="Wednesday">Wednesday</MenuItem>
                        <MenuItem value="Thursday">Thursday</MenuItem>
                        <MenuItem value="Friday">Friday</MenuItem>
                        <MenuItem value="Saturday">Saturday</MenuItem>
                        <MenuItem value="Sunday">Sunday</MenuItem>
                    </Select>
                </FormControl>
            </Box>
        
            <Box sx={{ minWidth: 120 }} style={{ marginTop: "30px" }}>
                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">Meals</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={meals}
                        label="Meals"
                        onChange={handleChangeMeals}
                    >
                        <MenuItem value="Breakfast">Breakfast</MenuItem>
                        <MenuItem value="Lunch">Lunch</MenuItem>
                        <MenuItem value="Snacks">Snacks</MenuItem>
                        <MenuItem value="Dinner">Dinner</MenuItem>
                    </Select>
                </FormControl>
            </Box>

            <Stack component="form" style={{ marginTop: "30px", marginLeft: "40px", backgroundColor: "white", width: "68%" }}>
                <TextField
                    id="date"
       
                    type="date"
                    defaultValue="2022-11-06"
                    sx={{ width: 200 }}
                    InputLabelProps={{
                        shrink: true,
                    }}
                />
            </Stack>
        </div>
    );
}