import React, { Fragment, useState } from 'react';
import { Link, Navigate} from 'react-router-dom';
import { connect } from 'react-redux';
import { logout } from '../actions/auth';
import {close} from '../dashboard/Sockettest' 
const Navbar = ({ logout, isAuthenticated }) => {
    const [redirect, setRedirect] = useState(false);

    const logout_user = () => {
        logout();
        setRedirect(true);
    };
    
    const resetRedirect = () => {
      <Navigate to='/login' /> 
      setRedirect(false);
  };
    const guestLinks = () => (
        <Fragment>
         
        </Fragment>
    );

    const authLinks = () => (
        <Fragment>

        <Link to="/login">
        <div id="regisbtn" style={{color: '#273152'}} onClick={logout_user}>
          <center><i>LOGOUT</i></center>	
        </div>
        </Link>
      
        </Fragment>
       
    );

    return (
        <Fragment>
        <div id="navbar" style={{position: 'fixed', width: '100%', height: '10%', padding: '5px'}}>
        <img src={require('../containers/KIIT-logo-HD.png')} style={{position: 'relative', float: 'left', height: '45px', margin: '2.5px', marginLeft: '10px'}} />
        
        
        {<Fragment>{ isAuthenticated ? authLinks() : guestLinks() }</Fragment>}
       
      </div>
           
            {redirect ? resetRedirect() : <Fragment></Fragment>}
        </Fragment>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { logout })(Navbar);
