import React, { useState} from 'react';
import { Link, Navigate } from 'react-router-dom';
import { connect } from 'react-redux';
import { signup } from '../actions/auth';
import axios from 'axios';
import Back_Auth from '../decoration/Back_Auth'


const Signup = ({ signup, isAuthenticated }) => {
    const [accountCreated, setAccountCreated] = useState(false);
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        password: '',
        re_password: ''
    });

    const { name, email, password, re_password } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        if (password === re_password) {
            signup(name, email, password, re_password);
            setAccountCreated(true);
        }
    };


    if (isAuthenticated) {
        return <Navigate to='/' />
    }
    if (accountCreated) {
        return <Navigate to='/login' />
    }
    
    return (<>
        <Back_Auth/>
        
        
        <div id="signupdiv" style={{zIndex:'2'}}>
        <p style={{margin: '27.5px', fontFamily: '"Roboto", sans-serix', fontSize: '35px', color: 'rgba(52, 40, 57, 1)', letterSpacing: '1.5px', fontWeight: 600, marginBottom: '10px'}}>
          Sign Up
        </p>
        <form onSubmit={e => onSubmit(e)}>
        <div className="signupinput">
          <input type="text" name="name" id="signupinputbox" placeholder="NAME" autocomplete="off" value={name} onChange={e => onChange(e)}/>
        </div>
        <div className="signupinput">
          <input type="email" name="email" id="signupinputbox" placeholder="EMAIL"  autocomplete="off" value={email} onChange={e => onChange(e)}/>
        </div>
        <div className="signupinput bigsignupinput">
          <input type="password" name="password" id="signupinputbox" placeholder="PASSWORD" value={password} onChange={e => onChange(e)} />
        </div>
        <div className="signupinput bigsignupinput">
          <input type="password" name="re_password" id="signupinputbox" placeholder="CONFIRM PASSWORD" value={re_password} onChange={e => onChange(e)} />
        </div>
        <div className="signupinput bigsignupinput checkdiv">  
          <input type="checkbox" id="check1" name="check1" style={{}} />
          <span htmlFor="check1" style={{fontWeight: 300, position: 'relative', top: '-2px', fontFamily: '"Roboto", sans-serif'}}> I have read and agreed to with Terms and Services and our Privacy Policy</span>
        </div>
        <div id="signupinput" style={{width: '100%', marginLeft: '0px'}}>  
          <center><button className="button" type='submit'>Create An Account</button></center>
        </div>	
        </form>
      </div>
        </>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { signup })(Signup);